@import url("https://fonts.googleapis.com/css2?family=Lato:wght@700&display=swap");

:root {
  --background-color-blue: #3ca7ee;
  --background-color-pink: #9b408f;
  --background-color-white: #ffffff;
  --success-color: #2691d9;
  --error-color: #d92626;
  --background-color-sliver: #c0c0c0;
  --background-color-black: #000000;
}

html {
  height: 100%;
}
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background: linear-gradient(#141e30, #243b55);
}

.containerLogin {
  position: relative;
  height: 350px;
  margin: 0 auto;
  margin-bottom: 35px;
  width: 400px;
  background-color: var(--background-color-white);
  border-radius: 15px;
}

.containerLogin > h1 {
  text-align: center;
  padding: 10px 0 20px 0;
  border-bottom: 1px solid var(--background-color-sliver);
}

.containerLogin > form {
  text-align: center;
  padding: 10px 40px;
  box-sizing: border-box;
}

form .txt_field {
  position: relative;
  margin: 20px 0;
  border-bottom: 2px solid var(--background-color-sliver);
}

.txt_field input {
  width: 100%;
  padding: 0 5px;
  height: 40px;
  font-size: 17px;
  border: none;
  outline: none;
}

.txt_field label {
  position: absolute;
  top: 50%;
  left: 5px;
  color: var(--background-color-sliver);
  transform: translateY(-50%);
  font-size: 16px;
  pointer-events: none;
  transition: 0.5s;
}

.txt_field span::before {
  content: "";
  position: absolute;
  top: 40px;
  left: 0;
  width: 100%;
  height: 2px;
  background: var(--background-color-sliver);
}

.txt_field input:focus ~ label,
.txt_field input:valid ~ label {
  top: -5px;
  color: var(--background-color-blue);
  position: absolute;
}

.txt_field input:focus ~ span::before,
.txt_field input:valid ~ span::before {
  width: 100%;
}

#btnlogin {
  font-weight: 700;
  width: 100%;
  height: 50px;
  margin: 20px 0;
  cursor: pointer;
  outline: none;
  border: 1px solid;
  border-radius: 25px;
  color: var(--background-color-white);
  font-size: 18px;
  background-color: var(--background-color-blue);
}

#btnlogin:hover {
  border-color: var(--background-color-blue);
  transition: 0.5s;
}

/* table */
#containerUser {
  font-family: "Montserrat", sans-serif;
  margin: 0 auto;
  font-weight: 520;
  border-radius: 10px 10px 0 0;
  width: 1200px;
}

.content-table {
  border-radius: 10px 10px 0 0;
  width: 100%;
  margin: 0 auto;
  background-color: var(--background-color-white);
  border-collapse: collapse;
  font-size: 16px;
}
.content-table thead {
  background-color: var(--background-color-blue);
}
.content-table thead tr {
  color: var(--background-color-white);
  text-align: left;
  font-weight: bold;
  text-align: center;
  background-color: var(--background-color-blue);
}

.content-table th,
.content-table td {
  padding: 12px 15px;
  text-align: center;
}

.content-table tbody tr {
  border-bottom: 1px solid var(--background-color-sliver);
  background-color: var(--background-color-white);
}
.content-table tbody tr:nth-of-type(even) {
  background-color: var(--background-color-white);
}

.content-table tbody tr:last-of-type {
  border-bottom: 2px solid var(--background-color-blue);
}
